	module.exports = {"pathContext":{"docHtml":"<h1><img src=\"screenshot.png\" width=\"400\" alt=\"boxen\"></h1>\n<blockquote>\n<p>Create boxes in the terminal</p>\n</blockquote>\n<p><a href=\"https://travis-ci.org/sindresorhus/boxen\"><img src=\"https://travis-ci.org/sindresorhus/boxen.svg?branch=master\" alt=\"Build Status\"></a></p>\n<h2>Install</h2>\n<pre><code>$ npm install boxen\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">const boxen = require('boxen');\n\nconsole.log(boxen('unicorn', {padding: 1}));\n/*\n┌─────────────┐\n│             │\n│   unicorn   │\n│             │\n└─────────────┘\n*/\n\nconsole.log(boxen('unicorn', {padding: 1, margin: 1, borderStyle: 'double'}));\n/*\n\n   ╔═════════════╗\n   ║             ║\n   ║   unicorn   ║\n   ║             ║\n   ╚═════════════╝\n\n*/\n</code></pre>\n<h2>API</h2>\n<h3>boxen(input, [options])</h3>\n<h4>input</h4>\n<p>Type: <code>string</code></p>\n<p>Text inside the box.</p>\n<h4>options</h4>\n<h5>borderColor</h5>\n<p>Type: <code>string</code><br>\nValues: <code>black</code> <code>red</code> <code>green</code> <code>yellow</code> <code>blue</code> <code>magenta</code> <code>cyan</code> <code>white</code> <code>gray</code></p>\n<p>Color of the box border.</p>\n<h5>borderStyle</h5>\n<p>Type: <code>string</code> <code>object</code><br>\nDefault: <code>single</code><br>\nValues:</p>\n<ul>\n<li>\n<p><code>single</code></p>\n<pre><code>┌───┐\n│foo│\n└───┘\n</code></pre>\n</li>\n<li>\n<p><code>double</code></p>\n<pre><code>╔═══╗\n║foo║\n╚═══╝\n</code></pre>\n</li>\n<li>\n<p><code>round</code> (<code>single</code> sides with round corners)</p>\n<pre><code>╭───╮\n│foo│\n╰───╯\n</code></pre>\n</li>\n<li>\n<p><code>single-double</code> (<code>single</code> on top and bottom, <code>double</code> on right and left)</p>\n<pre><code>╓───╖\n║foo║\n╙───╜\n</code></pre>\n</li>\n<li>\n<p><code>double-single</code> (<code>double</code> on top and bottom, <code>single</code> on right and left)</p>\n<pre><code>╒═══╕\n│foo│\n╘═══╛\n</code></pre>\n</li>\n<li>\n<p><code>classic</code></p>\n<pre><code>+---+\n|foo|\n+---+\n</code></pre>\n</li>\n</ul>\n<p>Style of the box border.</p>\n<p>Can be any of the above predefined styles or an object with the following keys:</p>\n<pre><code class=\"language-js\">{\n    topLeft: '+',\n    topRight: '+',\n    bottomLeft: '+',\n    bottomRight: '+',\n    horizontal: '-',\n    vertical: '|'\n}\n</code></pre>\n<h5>dimBorder</h5>\n<p>Type: <code>boolean</code><br>\nDefault: <code>false</code></p>\n<p>Reduce opacity of the border.</p>\n<h5>padding</h5>\n<p>Type: <code>number</code> <code>Object</code><br>\nDefault: <code>0</code></p>\n<p>Space between the text and box border.</p>\n<p>Accepts a number or an object with any of the <code>top</code>, <code>right</code>, <code>bottom</code>, <code>left</code> properties. When a number is specified, the left/right padding is 3 times the top/bottom to make it look nice.</p>\n<h5>margin</h5>\n<p>Type: <code>number</code> <code>Object</code><br>\nDefault: <code>0</code></p>\n<p>Space around the box.</p>\n<p>Accepts a number or an object with any of the <code>top</code>, <code>right</code>, <code>bottom</code>, <code>left</code> properties. When a number is specified, the left/right margin is 3 times the top/bottom to make it look nice.</p>\n<h5>float</h5>\n<p>Type: <code>string</code><br>\nValues: <code>right</code> <code>center</code> <code>left</code><br>\nDefault: <code>left</code></p>\n<p>Float the box on the available terminal screen space.</p>\n<h5>backgroundColor</h5>\n<p>Type: <code>string</code><br>\nValues: <code>black</code> <code>red</code> <code>green</code> <code>yellow</code> <code>blue</code> <code>magenta</code> <code>cyan</code> <code>white</code></p>\n<p>Color of the background.</p>\n<h5>align</h5>\n<p>Type: <code>string</code><br>\nDefault: <code>left</code><br>\nValues: <code>left</code> <code>center</code> <code>right</code></p>\n<p>Align the text in the box based on the widest line.</p>\n<h2>Related</h2>\n<ul>\n<li><a href=\"https://github.com/sindresorhus/boxen-cli\">boxen-cli</a> - CLI for this module</li>\n<li><a href=\"https://github.com/sindresorhus/cli-boxes\">cli-boxes</a> - Boxes for use in the terminal</li>\n</ul>\n<h2>License</h2>\n<p>MIT © <a href=\"https://sindresorhus.com\">Sindre Sorhus</a></p>","docPath":"argo-ci/node_modules/boxen/readme","proj":"argo-ci"}}

